import React, { useState, useEffect } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, ChevronsLeft, ChevronsRight
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'
import TenantComplains from './EstateManagerPages/TenantComplains'
import useJwt from '@src/auth/jwt/useJwt'

const Dash = () => {
    const userRole = JSON.parse(localStorage.getItem('userData')).role
    const Statuses = [
        { count: 0, id: 2, name: 'Completed' },
        { count: 0, id: 3, name: 'Pending' },
        { count: 0, id: 4, name: 'Cancelled' },
        { count: 0, id: 7, name: 'Accepted' },
        { count: 0, id: 10, name: 'Quotation Pending' },
        { count: 0, id: 11, name: 'Quotation Approved' },
        { count: 0, id: 12, name: 'Need Approval' },
        { count: 0, id: 13, name: 'Finished' }
    ]
    const usersIds = [
        { count: 0, id: 3, name: 'Landlord' },
        { count: 0, id: 4, name: 'Tenant' },
        { count: 0, id: 5, name: 'Contractor' }
    ]
    const [UsersEA, setUsersEA] = useState([
        {
            count: 0,
            name: 'Contractor'
        },
        {
            count: 0,
            name: 'Landlord'
        },
        {
            count: 0,
            name: 'Tenant'
        }
    ])
    const [complains, setcomplains] = useState([
        { count: 0, id: 3, name: 'Pending' },
        { count: 0, id: 12, name: 'Need Approval' },
        { count: 0, id: 7, name: 'Accepted' },
        { count: 0, id: 10, name: 'Quotation Pending' },
        { count: 0, id: 11, name: 'Quotation Approved' },
        { count: 0, id: 13, name: 'Finished' },
        { count: 0, id: 2, name: 'Completed' },
        { count: 0, id: 4, name: 'Cancelled' }
    ])
    const [property, setproperties] = useState([
        {
            count: 0,
            name: 'Total'
        },
        {
            count: 0,
            name: 'Occupied'
        },
        {
            count: 0,
            name: 'Unoccupied'
        }
    ])
    useEffect(() => {
        useJwt.dashboardSummary().then(res => {
            console.log("dashboardSummary", res.data.payload)
            const { complain_counter, property, user_counter } = res.data.payload
            const complaincounter = null
            const usercounter = null
            const restCOmplains = !complain_counter ? [] : Statuses.filter(c => false === complain_counter.some(cc => cc.status_id === c.id))
            const restUsers = !user_counter ? [] : usersIds.filter(u => false === user_counter.some(uu => uu.user_type === u.id))
            console.log(restUsers)
            setUsersEA(!user_counter ? UsersEA : [...user_counter.map(y => { return { count: y.number_of_user, name: y.user_type_name } }), ...restUsers])
            setcomplains(!complain_counter ? complains : [...complain_counter.map(x => { return { count: x.number_of_complain, name: x.status_name } }), ...restCOmplains]
            )
            setproperties(!property ? properties : [
                {
                    count: property.occupied_property.length + property.unoccupied_property.length,
                    name: 'Total'
                },
                {
                    count: property.occupied_property.length,
                    name: "Occupied"
                },
                {
                    count: property.unoccupied_property.length,
                    name: 'Unoccupied'
                }
            ])
        }).catch(error => {
            console.log("dashboardSummary", error)
            Error(error)
        })
    }, [])
    return (
        <>
            <Row>
                {UsersEA.map((x, i) => <Col key={i} sm={userRole === 'EstateManager' ? "4" : "3"}>
                    <Card>
                        <div className="text-center pt-1">
                            <h3><b>{x.count}</b></h3>
                            <p className='text-muted'>{x.name}</p>
                        </div>
                    </Card>
                </Col>)
                }
            </Row>
            <Card>
                <CardHeader>
                    <CardTitle tag='h4'>Property</CardTitle>
                </CardHeader>
                <CardBody className="d-flex flex-row justify-content-around flex-wrap">
                    {
                        property.map((x, i) => <div key={i} className="text-center pt-1 mx-1">
                            <h3><b>{x.count}</b></h3>
                            <p className='text-muted'>{x.name}</p>
                        </div>)
                    }
                </CardBody>
            </Card>
            {userRole !== 'Estatemanager' && <><Card>
                <CardHeader>
                    <CardTitle tag='h4'>Complains Overview</CardTitle>
                </CardHeader>
                <CardBody className="d-flex flex-row justify-content-around flex-wrap">
                    {
                        complains.map((x, i) => <div key={i} className="text-center pt-1 mx-1">
                            <h3><b>{x.count}</b></h3>
                            <p className='text-muted'>{x.name}</p>
                        </div>)
                    }
                </CardBody>
            </Card>
                <TenantComplains /></>}
        </>
    )
}

export default Dash